import { format } from "date-fns"

const defaultSaveName = (saveDataType: "search" | "watchlist") => {
  if (saveDataType === "watchlist") return "Untitled Watchlist"
  const dateFormat = "MMM dd yyyy, H:mm"
  const formattedDate = format(new Date(), dateFormat)
  return `Untitled Search - ${formattedDate}`
}

export { defaultSaveName }
